"use client";
import Button from '@/ui/Button';
import TextInput from '@/ui/TextInput';
import React, { useState, ChangeEvent } from 'react';
import BridgeList from './BridgeList';

interface FilterParams {
  stateCode: string;
  yearBuilt: string;
  functionalClass: string;
  owner: string;
}

interface Bridge {
  id: number; // Add specific fields as per your API response
  name: string;
  location: string;
}

const Map9: React.FC = () => {
  const [loading, setLoading] = useState<boolean>(false);
  const [filterParams, setFilterParams] = useState<FilterParams>({
    stateCode: '',
    yearBuilt: '',
    functionalClass: '',
    owner: '',
  });

  const [filteredBridges, setFilteredBridges] = useState<Bridge[]>([]);

  const handleFilterChange = (e: ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setFilterParams((prev) => ({
      ...prev,
      [name]: value,
    }));
  };

  const handleApplyFilters = async () => {
    try {
      setLoading(true);
      const res = await fetch('/api/bridges', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(filterParams),
      });

      if (!res.ok) {
        throw new Error('Failed to fetch bridges');
      }

      const data: Bridge[] = await res.json();
      setFilteredBridges(data);
    } catch (error) {
      console.error('Error fetching bridges:', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-3xl font-bold mb-6 text-black">Bridge Dashboard</h1>

      <div className="bg-white p-6 rounded-lg shadow-md mb-6">
        <h2 className="text-xl font-semibold mb-4">Filters</h2>
        <div className="grid grid-cols-2 gap-4">
          <TextInput
            label="State Code"
            placeholder="Enter State Code"
            value={filterParams.stateCode}
            onChange={(e) => handleFilterChange({ ...e, target: { ...e.target, name: 'stateCode' } })}
          />
          <TextInput
            label="Year Built"
            placeholder="Enter Year Built"
            value={filterParams.yearBuilt}
            onChange={(e) => handleFilterChange({ ...e, target: { ...e.target, name: 'yearBuilt' } })}
          />
          <TextInput
            label="Functional Class"
            placeholder="Enter Functional Class"
            value={filterParams.functionalClass}
            onChange={(e) => handleFilterChange({ ...e, target: { ...e.target, name: 'functionalClass' } })}
          />
          <TextInput
            label="Owner"
            placeholder="Enter Owner Code"
            value={filterParams.owner}
            onChange={(e) => handleFilterChange({ ...e, target: { ...e.target, name: 'owner' } })}
          />
        </div>
        <div className="mt-6">
          <Button
            onClick={handleApplyFilters}
            label={loading ? "Loading..." : "Apply Filters"}
          />
        </div>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {/* Uncomment and pass appropriate props when components are ready */}
        <BridgeList bridges={filteredBridges} />
        {/* <Map bridges={filteredBridges} /> */}
      </div>
    </div>
  );
};

export default Map9;
